import React, { Component } from 'react';
import { Alert, Button, ButtonToolbar, Col, Form, Modal, Row } from 'react-bootstrap';
import injectStyle from 'react-jss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTwitter, faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { faLanguage, faAt } from '@fortawesome/free-solid-svg-icons'
import { Fade } from 'react-reveal';


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      language: '',
      infoType: 'all',
      showEmailForm: false,
      emailForm: {
        from_name: '',
        from_email: '',
        message_html: '',
      },
      showAlert: false
    }
  }

  handleChange = (e) => {
    const currentInfo = this.state.emailForm;
    const field = e.target.id;
    currentInfo[field] = e.target.value;
    this.setState({ emailForm: currentInfo });
  }

  sendEmail = () => {
    window.Email.send({
      Host: 'smtp.google.com',
      Username: 'smtp22test@gmail.com',
      To: 'njdiaz90@gmail.com',
      From: this.state.emailForm.from_email,
      Subject: `${this.state.emailForm.from_name} - Contacto`,
      Body: this.state.emailForm.message_html,
      }
    ).then(
      this.setState({
        showEmailForm: false, showAlert: true,
        emailForm: { from_name: '', from_email: '', message_html: '' }
      })
    )
      .catch(err => console.error('Failed to send message. Error: ', err))
  }

  showAlert = () => {
    return (
      // <Fade when={this.state.showAlert}>
      <Alert dismissible show={this.state.showAlert} idx='success' variant='success'
        onClose={() => this.setState({ showAlert: false })}>Email sent! I'll be contacting you soon!</Alert>
      // </Fade>
    )
  }

  _renderEmailForm = () => {
    return (
      <Modal show={this.state.showEmailForm} onHide={() => this.setState({ showEmailForm: false })} aria-labelledby="contained-modal-title-vcenter" centered>
        <Modal.Header closeButton>
          <Modal.Title>Contact Me!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group controlId="from_name">
              <Form.Label>Name</Form.Label>
              <Form.Control type="text" placeholder="Enter your name" value={this.state.emailForm.from_name}
                onChange={this.handleChange} />
            </Form.Group>
            <Form.Group controlId="from_email">
              <Form.Label>Email address</Form.Label>
              <Form.Control type="email" placeholder="Enter email" value={this.state.emailForm.from_email}
                onChange={this.handleChange} />
              <Form.Text className="text-muted">
                We'll never share your email with anyone else.
              </Form.Text>
            </Form.Group>
            <Form.Group controlId="message_html">
              <Form.Label>Your Message</Form.Label>
              <Form.Control type="text" placeholder="Enter your message..." value={this.state.emailForm.message_html}
                onChange={this.handleChange} />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => this.setState({ showEmailForm: false })}>
            Close
            </Button>
          <Button variant="primary" onClick={this.sendEmail}>
            Send
            </Button>
        </Modal.Footer>
      </Modal>
    )
  }


  render() {
    const { classes } = this.props;
    return (
      <Col xs={12} md={{ span: 8, offset: 2 }}>
        <div className={classes.appWrapper}>
          {this._renderEmailForm()}
          <div className={classes.alert}>
            {this.showAlert()}
          </div>
          <div>
            <Fade>
              <Col md={{ span: 8, offset: 2 }}>
                <h1 className={classes.headerTitle}>Nicolás Díaz Soler</h1>
              </Col>
            </Fade>
          </div>
          <Fade delay={500} cascade>
            <Col xs={12}>
              <div className={classes.text}>
                <p>Argentinian English-Spanish Translator, specialised in Marketing, Software &amp; Videogames. Grammar and pronunciation nerd. <a href='https://en.wikipedia.org/wiki/English_as_a_second_or_foreign_language'>ESL</a> teacher. British English, mate.</p>
              </div>
            </Col>
          </Fade>
          <Fade delay={1000} cascade>
            <Col>
              <div className={classes.text}>
                <p>Junior Software Developer. JavaScript, ReactJS &amp; MySQL. Looking forward to jumping into React Native.</p>
              </div>
            </Col>
          </Fade>
          <Fade delay={1500} cascade>
            <ButtonToolbar className={classes.iconToolbar}>
              <Row className={classes.iconToolbar}>
                <Col xs={2} md={2}>
                  <div className={classes.socialIcon}><a href="https://twitter.com/njdiaz90/"><FontAwesomeIcon icon={faTwitter} size="3x" className={classes.icon} /></a></div>
                </Col>
                <Col xs={2} md={2}>
                  <div className="social-icon"><a href="https://github.com/njdiazsoler/"><FontAwesomeIcon icon={faGithub} size="3x" className={classes.icon} /></a></div>
                </Col>
                <Col xs={2} md={2}>
                  <div className="social-icon"><a href="https://www.linkedin.com/in/njdst/"><FontAwesomeIcon icon={faLinkedin} size="3x" className={classes.icon} /></a></div>
                </Col >
                <Col xs={2} md={2}>
                  <div className="social-icon"><a href="https://www.upwork.com/o/profiles/users/_~014e308c50ce78460b/"><FontAwesomeIcon icon={faLanguage} size="3x" className={classes.icon} /></a></div>
                </Col>
                <Col xs={2} md={2}>
                  <div className="social-icon"><Button variant='link' onClick={() => this.setState({ showEmailForm: true })}><FontAwesomeIcon icon={faAt} size="3x" className={classes.icon} /></Button></div>
                </Col>
              </Row>
            </ButtonToolbar>
          </Fade>
        </div>
      </Col>
    );
  }
}

const styles = {
  alert: {
    left: '0%',
    minWidth: '100%',
    position: 'absolute',
    textAlign: 'center',
    top: '0',
    zIndex: '9',
  },
  appLink: {
    color: '#61dafb',
  },
  appWrapper: {
    color: 'white',
    display: 'flex',
    flexFlow: 'column',
    justifyContent: 'space-around',
    minHeight: '100vh',
    padding: '0',
    textAlign: 'center',
  },
  headerTitle: {
    fontSize: '3rem',
  },
  iconToolbar: {
    display: 'flex',
    justifyContent: 'space-evenly',
  },
  icon: {
    color: 'white',
    borderRadius: '50%',
  },
  text: {
    fontSize: '1.25rem'
  }
}

export default injectStyle(styles)(App);
